.comment .avatar{border:2px solid #3EC4AF;min-width:30px;margin-right:15px;position:relative;z-index:2;width:50px !important;height:50px !important}.comment .avatar img{width:50px !important;height:50px !important}.comment{display:-webkit-box;display:-ms-flexbox;display:flex;position:relative;font-size:12px}.comment.welcome:before{border:0}.comment--me{-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end}.comment__message{padding-top:5px;line-height:1.7em;min-height:68px;padding-bottom:15px;margin-bottom:15px;width:100%;max-width:200px;background:#F4F4F4;border:1px solid #dbdbdb;padding:10px;border-radius:5px;position:relative}.comment--me .comment__message{background:rgba(0,111,94,0.1);border:1px solid rgba(0,111,94,0.3)}.welcome__message{padding-top:5px;line-height:1.7em;min-height:68px;padding-bottom:15px;margin-bottom:15px;width:100%;background:#F4F4F4;padding:10px;border-radius:5px;position:relative;box-shadow:0 3px 5px rgba(0,0,0,0.15)}.comment__message p{margin:0}.comment__info{display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-pack:justify;-ms-flex-pack:justify;justify-content:space-between;border-bottom:1px dotted #ccc;margin-bottom:7px}.comment__username{font-weight:bold;line-height:1;color:#71cd9d}.comment__time{font-size:10px;color:#999}.image-container{width:100px;height:100px;overflow:hidden;padding:10px;border:1px solid #ccc;border-radius:5px}.image-container img{width:100%}.file-container a{display:-webkit-box;display:-ms-flexbox;display:flex;color:#2c3e50}.file-container i.fa{position:relative;font-size:3em;color:#2c3e50;float:left}.file-container i.fa .ft{position:absolute;top:10px;margin-left:5px;font-size:12px;background:transparent}.file-container i.fa .fe{font-size:16px;background:#2c3e50;color:#fff;padding:2px 6px 4px 4px;margin-top:-12px;margin-left:10px;border-radius:2px}.file-container .file-name{word-break:break-word;line-height:1.4;float:left;padding-top:5px;padding-left:5px}



